"use client";

import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Menu, X } from "lucide-react";
import { useState, useRef, useEffect } from "react";
import { cn } from "@/lib/utils";
import Image from "next/image";

const MongoDBStyleTopNav = () => {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [activeDropdown, setActiveDropdown] = useState<string | null>(null);
  const mobileMenuRef = useRef<HTMLDivElement>(null);
  const dropdownRefs = useRef<{ [key: string]: HTMLDivElement | null }>({});

  // Handle escape key to close menus
  useEffect(() => {
    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        setMobileMenuOpen(false);
        setActiveDropdown(null);
      }
    };

    document.addEventListener('keydown', handleEscape);
    return () => document.removeEventListener('keydown', handleEscape);
  }, []);

  // Handle click outside to close dropdowns
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (activeDropdown && dropdownRefs.current[activeDropdown] && 
          !dropdownRefs.current[activeDropdown]?.contains(event.target as Node)) {
        setActiveDropdown(null);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [activeDropdown]);

  const toggleDropdown = (dropdown: string) => {
    setActiveDropdown(activeDropdown === dropdown ? null : dropdown);
  };

  const handleMobileMenuToggle = () => {
    setMobileMenuOpen(!mobileMenuOpen);
    // Focus management for mobile menu
    if (!mobileMenuOpen) {
      setTimeout(() => {
        const firstMenuItem = mobileMenuRef.current?.querySelector('button, a');
        if (firstMenuItem instanceof HTMLElement) {
          firstMenuItem.focus();
        }
      }, 100);
    }
  };

  return (
    <header className="sticky top-0 z-50 w-full border-b border-gray-border bg-white" role="banner">
      <div className="container mx-auto px-4 flex h-16 items-center justify-between">
        {/* Logo */}
        <Link 
          href="/" 
          className="flex items-center gap-3 transition-opacity hover:opacity-80 focus:outline-none focus:ring-2 focus:ring-mongo-accent focus:ring-offset-2 rounded-sm"
          aria-label="VBUV-Cloud home"
        >
          <Image
            src="/favicon.ico"
            alt=""
            width={32}
            height={32}
            className="h-8 w-8"
            role="presentation"
          />
          <span className="text-xl font-bold text-gray-900">VBUV-Cloud</span>
        </Link>

        {/* Desktop Navigation */}
        <nav className="hidden lg:flex items-center gap-6 xl:gap-8" aria-label="Main navigation" role="navigation">
          {[
            { name: 'Products', hasDropdown: true },
            { name: 'Resources', hasDropdown: true },
            { name: 'Solutions', hasDropdown: true },
            { name: 'Company', hasDropdown: true }
          ].map((item) => (
            <div key={item.name} className="relative" ref={el => { dropdownRefs.current[item.name] = el; }}>
              <button 
                className="mongodb-nav-link text-sm font-medium px-2 py-1 flex items-center gap-1 whitespace-nowrap"
                aria-haspopup="true"
                aria-expanded={activeDropdown === item.name}
                onClick={() => toggleDropdown(item.name)}
                onKeyDown={(e) => {
                  if (e.key === 'Enter' || e.key === ' ') {
                    e.preventDefault();
                    toggleDropdown(item.name);
                  }
                }}
              >
                {item.name}
                <svg 
                  className={cn("w-4 h-4 transition-transform", activeDropdown === item.name && "rotate-180")} 
                  fill="none" 
                  stroke="currentColor" 
                  viewBox="0 0 24 24" 
                  aria-hidden="true"
                >
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                </svg>
              </button>
              
              {/* Dropdown placeholder - would contain actual menu items in real implementation */}
              {activeDropdown === item.name && (
                <div 
                  className="absolute top-full left-0 mt-1 w-48 mongodb-card z-50"
                  role="menu"
                  aria-labelledby={`${item.name}-button`}
                >
                  <div className="py-2">
                    <div className="px-4 py-2 text-sm text-gray-text">
                      {item.name} menu items would go here
                    </div>
                  </div>
                </div>
              )}
            </div>
          ))}
          
          <Link 
            href="/pricing" 
            className="mongodb-nav-link text-sm font-medium px-2 py-1 whitespace-nowrap"
          >
            Pricing
          </Link>
        </nav>

        {/* Desktop Actions */}
        <div className="hidden lg:flex items-center gap-3">
          <Button 
            variant="outline" 
            className="mongodb-button-outline text-sm"
            aria-label="Sign in to your account"
          >
            Sign In
          </Button>
        </div>

        {/* Mobile Menu Button */}
        <Button
          variant="ghost"
          size="icon"
          className="lg:hidden focus:ring-2 focus:ring-blue-600 focus:ring-offset-2"
          onClick={handleMobileMenuToggle}
          aria-expanded={mobileMenuOpen}
          aria-controls="mobile-menu"
          aria-label={mobileMenuOpen ? "Close navigation menu" : "Open navigation menu"}
        >
          {mobileMenuOpen ? <X className="h-5 w-5" aria-hidden="true" /> : <Menu className="h-5 w-5" aria-hidden="true" />}
        </Button>
      </div>

        {/* Mobile Menu */}
        <div
          id="mobile-menu"
          ref={mobileMenuRef}
          className={cn(
            "lg:hidden overflow-hidden transition-all duration-300 ease-in-out bg-white border-b border-gray-border",
            mobileMenuOpen ? "max-h-96" : "max-h-0"
          )}
          aria-hidden={!mobileMenuOpen}
        >
          <nav className="container mx-auto px-4 flex flex-col gap-4 py-4" aria-label="Mobile navigation">
            {['Products', 'Resources', 'Solutions', 'Company'].map((item) => (
              <button 
                key={item}
                className="mongodb-nav-link text-sm font-medium px-2 py-1 text-left"
                aria-label={`${item} menu`}
              >
                {item}
              </button>
            ))}
            
            <Link 
              href="/pricing" 
              className="mongodb-nav-link text-sm font-medium px-2 py-1"
              onClick={() => setMobileMenuOpen(false)}
            >
              Pricing
            </Link>
            
            <div className="flex gap-3 pt-2">
              <Button 
                variant="outline" 
                className="mongodb-button-outline flex-1"
                aria-label="Sign in to your account"
              >
                Sign In
              </Button>
            </div>
          </nav>
        </div>
    </header>
  );
};

export default MongoDBStyleTopNav;