"use client";

import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Sparkles } from "lucide-react";
import { useState, useRef, useEffect } from "react";
import { cn } from "@/lib/utils";

const MongoDBStyleSubNav = () => {
  const [activeDropdown, setActiveDropdown] = useState<string | null>(null);
  const dropdownRefs = useRef<{ [key: string]: HTMLDivElement | null }>({});

  // Handle escape key to close dropdowns
  useEffect(() => {
    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        setActiveDropdown(null);
      }
    };

    document.addEventListener('keydown', handleEscape);
    return () => document.removeEventListener('keydown', handleEscape);
  }, []);

  // Handle click outside to close dropdowns
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (activeDropdown && dropdownRefs.current[activeDropdown] && 
          !dropdownRefs.current[activeDropdown]?.contains(event.target as Node)) {
        setActiveDropdown(null);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [activeDropdown]);

  const toggleDropdown = (dropdown: string) => {
    setActiveDropdown(activeDropdown === dropdown ? null : dropdown);
  };
  return (
    <nav className="border-b border-gray-border bg-gray-bg" aria-label="University navigation" role="navigation">
      <div className="container mx-auto px-4 flex h-14 items-center justify-between">
        <div className="flex items-center gap-2 sm:gap-4 md:gap-6 lg:gap-8 overflow-x-auto scrollbar-hide">
          <Link 
            href="/courses" 
            className="mongodb-nav-link text-sm font-medium px-2 py-1 whitespace-nowrap flex-shrink-0"
            aria-current="page"
          >
            VBUV-Cloud University
          </Link>
          <Link 
            href="/courses" 
            className="mongodb-nav-link text-sm font-medium px-2 py-1 whitespace-nowrap flex-shrink-0 hidden sm:block"
          >
            Self-paced Learning
          </Link>
          
          <div className="relative hidden md:block flex-shrink-0" ref={el => { dropdownRefs.current['training'] = el; }}>
            <button 
              className="mongodb-nav-link text-sm font-medium px-2 py-1 flex items-center gap-1 whitespace-nowrap"
              aria-haspopup="true"
              aria-expanded={activeDropdown === 'training'}
              onClick={() => toggleDropdown('training')}
              onKeyDown={(e) => {
                if (e.key === 'Enter' || e.key === ' ') {
                  e.preventDefault();
                  toggleDropdown('training');
                }
              }}
            >
              Instructor-led Training
              <svg 
                className={cn("w-4 h-4 transition-transform", activeDropdown === 'training' && "rotate-180")} 
                fill="none" 
                stroke="currentColor" 
                viewBox="0 0 24 24" 
                aria-hidden="true"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </button>
            
            {/* Training Dropdown */}
            {activeDropdown === 'training' && (
              <div 
                className="absolute top-full left-0 mt-1 w-56 mongodb-card z-50"
                role="menu"
                aria-labelledby="training-button"
              >
                <div className="py-2">
                  <div className="px-4 py-2 text-sm text-gray-text">
                    Training options would go here
                  </div>
                </div>
              </div>
            )}
          </div>
          
          <div className="relative hidden lg:block flex-shrink-0" ref={el => { dropdownRefs.current['credentials'] = el; }}>
            <button 
              className="mongodb-nav-link text-sm font-medium px-2 py-1 flex items-center gap-1 whitespace-nowrap"
              aria-haspopup="true"
              aria-expanded={activeDropdown === 'credentials'}
              onClick={() => toggleDropdown('credentials')}
              onKeyDown={(e) => {
                if (e.key === 'Enter' || e.key === ' ') {
                  e.preventDefault();
                  toggleDropdown('credentials');
                }
              }}
            >
              Credentials
              <svg 
                className={cn("w-4 h-4 transition-transform", activeDropdown === 'credentials' && "rotate-180")} 
                fill="none" 
                stroke="currentColor" 
                viewBox="0 0 24 24" 
                aria-hidden="true"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </button>
            
            {/* Credentials Dropdown */}
            {activeDropdown === 'credentials' && (
              <div 
                className="absolute top-full left-0 mt-1 w-48 mongodb-card z-50"
                role="menu"
                aria-labelledby="credentials-button"
              >
                <div className="py-2">
                  <div className="px-4 py-2 text-sm text-gray-text">
                    Credential options would go here
                  </div>
                </div>
              </div>
            )}
          </div>
          
          <Link 
            href="/courses" 
            className="mongodb-nav-link text-sm font-medium px-2 py-1 whitespace-nowrap flex-shrink-0 hidden xl:block"
          >
            Skills
          </Link>
        </div>
        
        <div className="flex items-center gap-3 flex-shrink-0 ml-4">
          <Button 
            variant="outline" 
            size="sm" 
            className="mongodb-button-outline flex items-center gap-2 text-xs md:text-sm whitespace-nowrap"
            aria-label="Access VBUV-Cloud AI assistant"
          >
            <Sparkles className="h-3 w-3 md:h-4 md:w-4 flex-shrink-0" aria-hidden="true" />
            <span className="hidden sm:inline">VBUV-Cloud AI</span>
            <span className="sm:hidden">AI</span>
          </Button>
        </div>
      </div>
    </nav>
  );
};

export default MongoDBStyleSubNav;